<template>
		<div class="posts">
    <div>
      <van-tabs v-model="active" swipeable  @click="changetab">
  <van-tab v-for="item in posttype" 
  :key="item.tid"
  :title="item.type"
>
     <ul>
      <li v-for="item in postdata" 
      :key="item.tid">
      <router-link :to="{
                   name:'Post_detail',
                   params:{post_id:item.tid,type:posttype[item.type].type}
                 }">
    <div v-show="active == item.type || active ==0" @click="goto_detail">
        <span>发帖人：{{item.poster}}</span>
        <h2>标题：{{item.title}}</h2>
        <p>发帖内容：{{item.content}}</p>
       <p>类型：{{posttype[item.type].type}}</p>
        <p>日期：{{item.date}}</p>

      </div>
        </router-link>
        </li>
  </ul>
      </van-tab>
</van-tabs>
    </div>

     </div>  
  
  

</template>

<script >
 
	export default {
  name: "Posts",
  components: {
    
  },
  props:{
    postdata:{
      type:Array,
      default(){
        return  null;
      }
    }


  },
  data()
  {
    return{
      active:0,
    posttype:[
    {
      type:"全部",
      tid:0,
    },
      {
      type:"表白",
      tid:1,
    },
    {
      type:"交友",
      tid:2,
    },
    {
      type:"寻物启事",
      tid:3,
    },
    ]

  }
},
  
        computed: {
          currentTabComponent: function() {
            return this.currentTab;
          }
        }, 
         methods: {
   changetab(name){
      this.active=name;
     
    },
    
  },

     }


</script>

